	import {  
        mapMutations  
    } from 'vuex';
	
	export default{
		data(){
			return {
				mobile: '',
				password: '',
				logining: false,
				
				//微信登陆
				wxCode: null,
				userInfo:{},
				needLogin:true,
				needGetMobile:false,
			}
		},
		onLoad(){
			// 获取微信code 并且请求链接
			var that = this;
			uni.login({
			  provider: 'weixin',
			  success: function (loginRes) {
				that.wxCode = loginRes.code;
			  }
			});
		},
		methods: {
			...mapMutations(['login','setToken']),
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			navBack(){
				uni.navigateBack();
			},
			toRegist(){
				this.$api.msg('去注册');
			},
			async toLogin(){
				this.logining = true;
				
				const {mobile, password} = this;
				/* 数据验证模块
				if(!this.$api.match({
					mobile,
					password
				})){
					this.logining = false;
					return;
				}
				*/
				const sendData = {
					mobile,
					password
				};
				const result = await this.$api.json('userInfo');
				if(result.status === 1){
					this.login(result.data);
                    uni.navigateBack();  
				}else{
					this.$api.msg(result.msg);
					this.logining = false;
				}
			},
			handleContact(e){
				console.log(e.path)
				console.log(e.query)
			},
			bindGetUserInfo(e){
				let res = e.detail;
				if(res.errMsg != 'getUserInfo:ok'){
					return;
				}
				this.loginRequest(e.detail);
			},
			getPhoneNumber (e) {
				let res = e.detail;
				if(res.errMsg != 'getPhoneNumber:ok'){
					return;
				}
				let value = {encryptedData: res.encryptedData,iv: res.iv};
				let url = this.$api.config('api') + "/h5/user/bindMobile";
				this.$http.requestPost(url,value,{}).then(result => {
					if(result.code == 'ok' && result.data){
						let data = result.data;
						if(data.mobile == null || data.mobile == ''){
							this.$api.msg('获取手机号码异常，请重新授权登陆');
							return;
						}
						let userInfo = this.userInfo;
						userInfo['mobile'] = data.mobile;
						this.login(userInfo);
						uni.navigateBack();  
					}else{
						this.$api.msg(result.msg);
						this.logining = false;
					}
				});
			},
			async loginRequest(res){
				let userInfo = res.userInfo
				let value = {code: this.wxCode,encryptedData: res.encryptedData,iv: res.iv};
				let result = await this.$http.requestPost(this.$api.config('api') + '/h5/auth/user/wxapp/signIn',value,{});
				if(result.code == 'ok' && result.data){
					this.needLogin = false;
					let data = result.data;
					let userInfoVo = {
						id: data.userId,
						mobile: data.mobile,
						nickname: userInfo.nickName,
						portrait: userInfo.avatarUrl,
						token: data.token
					};
					this.userInfo = userInfoVo;
					this.setToken(data.token);
					if(data.mobile && data.mobile != '' ){
						this.login(userInfoVo);
						uni.navigateBack();  
					}else{
						this.needGetMobile = true;
					}
				}else{
					this.needLogin = true;
					this.$api.msg(result.msg);
					this.logining = false;
				}
			}
		},
	}
